.kontakt-container {
  sheight: 500px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  .arrow {
    width: 100%;
    text-align: center; }

  .contact-info {
    sbackground-color: pink;
    margin-top: 50px;
    .icon {
      color: royalblue;
      font-size: 24px; }
    p, h3 {
      margin: 20px;
      font-size: 24px;
      border-bottom: 1px dotted black; }
    h3 {
      border-bottom: none;
      margin: 10px; } }

  .contact-form {
    display: flex;
    flex-direction: column;
    align-content: center;
    width: 300px;
    sbackground-color: blue;
    margin-top: 50px;
    h3 {
      margin: 10px;
      font-size: 24px; }

    .row {
      sbackground-color: green;
      text-align: center;
      display: flex;
      margin: 5px;
      input, textarea {
        width: 80%;
        resize: none;
        margin: 5px;
        padding: 10px;
        sbackground-color: lightgrey;
        border-width: 1px;
        border-style: solid;
        font-family: Arial;
        font-size: 16px;
        border-radius: 5px;
        letter-spacing: 1px; }
      .isOK {
        width: 25px;
        height: 25px;
        float: right;
        sbackground-color: pink;
        text-align: center; }
      .error {
        color: #f23333;
        text-align: left;
        margin-left: 5px; } }
    input.button {
      width: 50%;
      background-color: royalblue;
      color: white; }
    input.button:hover {
      background-color: blue;
      transition: 0.4s linear; } }

  .photo-container {
    margin: 20px;
    margin-top: 50px;
    sbackground-color: pink;
    .photo {
      width: 300px;
      height: 300px;
      sbackground-color: pink;
      position: relative;
      border-radius: 50%;
      overflow: hidden;
      img {
        swidth: 100%;
        sheight: 100%;
        position: absolute;
        top: 70%;
        left: 50%;
        transform: translate(-50%, -50%) scale(0.5); } }
    .desc {
      text-align: center;
      margin-top: 20px;
      h3 {
        color: royalblue;
        letter-spacing: 1px; } } } }

@media ( max-width: 600px ) {
  .kontakt-container {
    flex-direction: column;
    align-items: center;
    sheight: 800px;
    .contact-info {
      width: 300px;
      h3 {
        font-size: 24px; }
      p {
        font-size: 20px; } }

    .contact-form {}
    .photo-container {
      margin: 40px 0px 40px 0px; } } }
